.wrapper {
  max-width: 1335px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 20px;
  margin: auto; }
  .wrapper a {
    display: inline-block;
    margin: 5px; }
    .wrapper a:first-child {
      margin-left: 0; }

.slider-wrapper {
  position: relative;
  height: 100vh;
  overflow: hidden; }

.slide {
  height: 100vh;
  background-size: cover !important; }
  .slide::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100vH;
    background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(0, 0, 0, 0.9)));
    background: -webkit-linear-gradient(transparent, rgba(0, 0, 0, 0.9));
    background: -o-linear-gradient(transparent, rgba(0, 0, 0, 0.9));
    background: linear-gradient(transparent, rgba(0, 0, 0, 0.9));
    bottom: 0;
    left: 0; }

.previousButton, .nextButton {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 10;
  background: url(../img/arrow.svg) no-repeat center center/16px;
  /*'http://svgshare.com/i/41_.svg'*/
  width: 32px;
  height: 32px;
  text-indent: -9999px;
  cursor: pointer; }

.previousButton:hover, .nextButton:hover {
  background: url(../img/arrow.svg) no-repeat center center/16px; }

.previousButton {
  left: 0;
  -webkit-transform: rotate(180deg) translateY(calc(50% + 0px));
  -ms-transform: rotate(180deg) translateY(calc(50% + 0px));
      transform: rotate(180deg) translateY(calc(50% + 0px)); }
  .previousButton:hover {
    left: -10px; }

.nextButton {
  right: 0; }
  .nextButton:hover {
    right: -10px; }

.slider-content {
  text-align: center; }
  .slider-content .inner {
    padding: 0 50px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: absolute;
    width: 40%;
    top: 50%;
    right: -95px;
    font-family: 'font-file-82132';
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%); }
    .slider-content .inner button {
      -webkit-transition-delay: 0s;
      -o-transition-delay: 0s;
         transition-delay: 0s; }
    .slider-content .inner h1 {
      font-weight: 900;
      margin: 0 auto;
      max-width: 840px;
      color: #08fdd8;
      letter-spacing: 2px;
      font-size: 64px;
      line-height: 1; }
    .slider-content .inner p {
      color: #FFFFFF;
      font-size: 14px;
      line-height: 1.5;
      margin: 20px auto 30px;
      max-width: 640px;
      font-family: "Open Sans", sans-serif; }
  .slider-content section {
    position: absolute;
    bottom: 20px;
    left: 20px; }
    .slider-content section span {
      color: #FFFFFF;
      color: rgba(255, 255, 255, 0.5);
      font-size: 12px;
      display: inline-block;
      text-align: left;
      line-height: 1.4;
      vertical-align: middle;
      margin-left: 10px; }
    .slider-content section img {
      width: 40px;
      height: 40px;
      border: solid 2px rgba(255, 255, 255, 0.5);
      border-radius: 100%;
      vertical-align: middle; }
    .slider-content section span strong {
      color: #FFFFFF;
      font-size: 14px;
      display: block; }

