.bg {
  position: relative;
  z-index: 0;
  width: 400px;
  height: 609px;
  position: absolute;
  top: 0;
  right: 15%;
  bottom: 0;
  left: auto;
  margin: auto;
  font-family: 'Bowlby One SC', cursive;
  font-size: 39vw;
  width: 100vw;
  margin-top: calc(50vh - 10vw);
  text-align: center;
  -webkit-transform: rotateZ(-18deg) rotateY(-30deg);
          transform: rotateZ(-18deg) rotateY(-30deg);
  background: -webkit-linear-gradient(30deg, #fc0853 10%, #fc0853 52%, #08fdd8 52%, #08fdd8 72%);
  background: -o-linear-gradient(30deg, #fc0853 10%, #fc0853 52%, #08fdd8 52%, #08fdd8 72%);
  background: linear-gradient(60deg, #fc0853 10%, #fc0853 52%, #08fdd8 52%, #08fdd8 72%);
  /*text-shadow: 0.5px -0.6vw #fff4;*/
  color: #fff;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: 5s BeProud linear 1, 2.5s Always ease alternate 2;
          animation: 5s BeProud linear 1, 2.5s Always ease alternate 2;
  z-index: 0;
  width: 432px;
  height: 574px;
  position: absolute;
  top: -50px;
  right: 15%;
  bottom: 0;
  left: auto;
  margin: 0;
  overflow: hidden; }

@-webkit-keyframes BeProud {
  100% {
    background-position: 100vw 0px; } }

@keyframes BeProud {
  100% {
    background-position: 100vw 0px; } }

/*Preloader----css start*/
.preloader {
  position: fixed;
  will-change: transform;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: #252627;
  z-index: 2;
  display: none;
  overflow: hidden;
  height: 100%;
  width: 100%;
  -webkit-animation: animateright 0.8s ease 1s;
          animation: animateright 0.8s ease 1s; }
  .preloader .inner {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 400px;
    max-width: 100%;
    height: 160px;
    text-align: center; }
    .preloader .inner > .logo {
      display: block;
      margin: 0 auto;
      -webkit-text-fill-color: transparent;
      display: block;
      padding: 8px 0;
      display: block;
      padding: 8px 0;
      font-size: 2rem;
      text-align: center;
      font-family: 'Bowlby One SC', cursive;
      background: -webkit-linear-gradient(30deg, #fc0853 10%, #fc0853 51%, #08fdd8 51%, #08fdd8 72%);
      background: -o-linear-gradient(30deg, #fc0853 10%, #fc0853 51%, #08fdd8 51%, #08fdd8 72%);
      background: linear-gradient(60deg, #fc0853 10%, #fc0853 51%, #08fdd8 51%, #08fdd8 72%);
      /* text-shadow: 0.5px -0.6vw #fff4; */
      color: #fff;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; }
    .preloader .inner > span {
      color: #8d8d8d;
      margin-top: 9px;
      margin-bottom: 30px;
      display: block; }
      .preloader .inner > span.thinking {
        font-size: 9px;
        font-family: "Open Sans", sans-serif;
        letter-spacing: 3px; }
  .preloader .progress-bar {
    text-align: left; }
  .preloader .progress-bar_bg {
    height: 1px;
    background: #047765; }
    .preloader .progress-bar_bg div {
      height: 1px;
      background: #08fdd8;
      width: 0;
      -webkit-animation: 1s Progress linear 1;
              animation: 1s Progress linear 1; }

.showPage {
  -webkit-animation: 0.5s fadein ease 1;
          animation: 0.5s fadein ease 1; }

@-webkit-keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes Progress {
  100% {
    width: 100%; } }

@keyframes Progress {
  100% {
    width: 100%; } }

@-webkit-keyframes animateright {
  from {
    left: 0%; }
  to {
    left: 100%; } }

@keyframes animateright {
  from {
    left: 0%; }
  to {
    left: 100%; } }

.preloader .progress-bar span {
  color: #fff;
  text-align: right;
  float: left;
  font-size: 27px;
  font-family: font-file-82132;
  margin-bottom: 10px;
  display: block; }

/*Preloader----css end*/
