    background: -webkit-linear-gradient(30deg, #fc0853 10%, #fc0853 63%, #08fdd8 63%, #08fdd8 72%);
    background: -o-linear-gradient(30deg, #fc0853 10%, #fc0853 63%, #08fdd8 63%, #08fdd8 72%);
