.nav_bar {
  background: #171717;
  width: 60px;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 3;
  min-height: 500px; }
  .nav_bar nav {
    display: block;
    text-align: center;
    position: absolute;
    height: 210px;
    top: 50%;
    margin-top: -120px;
    width: 100%; }
    .nav_bar nav a {
      font-size: 22px;
      color: #808081;
      display: block;
      line-height: 51px;
      height: 51px;
      position: relative;
      text-decoration: none;
      cursor: pointer; }
      .nav_bar nav a svg {
        -webkit-transition: all 0.3s ease-out;
        -o-transition: all 0.3s ease-out;
        transition: all 0.3s ease-out; }
      .nav_bar nav a:hover span {
        opacity: 0; }
      .nav_bar nav a.active, .nav_bar nav a:hover {
        color: #08fdd8; }
      .nav_bar nav a:after {
        content: '';
        font-size: 9px;
        letter-spacing: 2px;
        position: absolute;
        bottom: 0px;
        display: block;
        width: 100%;
        text-align: center;
        opacity: 0;
        -webkit-transition: all 0.3s ease-out;
        -o-transition: all 0.3s ease-out;
        transition: all 0.3s ease-out; }
      .nav_bar nav a:first-child:after {
        content: 'HOME'; }
      .nav_bar nav a:first-child + a:after {
        content: 'ABOUT'; }
      .nav_bar nav a:first-child + a + a:after {
        content: 'SKILLS'; }
      .nav_bar nav a:first-child + a + a + a:after {
        content: 'MY WORK'; }
      .nav_bar nav a:first-child + a + a + a + a:after {
        content: 'CONTACT'; }
      .nav_bar nav a:hover:after {
        opacity: 1; }
  .nav_bar ul {
    position: absolute;
    bottom: 20px;
    width: 100%;
    display: block;
    padding: 0;
    list-style: none;
    text-align: center;
    margin: 0; }
    .nav_bar ul li a {
      padding: 7px 0;
      display: block;
      font-size: 15px;
      line-height: 16px;
      color: #808081; }
      .nav_bar ul li a:hover {
        color: #08fdd8; }

@media (max-width: 960px) {
  .nav_bar {
    width: 98%;
    height: 60px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    min-height: 0;
    padding-left: 2%; }
  .logo {
    float: left;
    padding: 2px 0;
    margin-right: 20px; }
    .logo img {
      display: block;
      margin: 5px 15px;
      width: auto;
      height: auto; }
    .logo > img:first-child {
      height: 30px; }
  .lang {
    float: left;
    width: auto;
    margin-top: 0;
    line-height: 60px; }
    .lang a {
      margin-top: 0;
      display: inline;
      margin-right: 7px; }
  .nav_bar nav {
    width: 42%;
    position: absolute;
    left: 50%;
    margin-left: -19%;
    float: left;
    overflow: hidden;
    text-align: center;
    height: 60px;
    top: 0;
    margin-top: 0; }
    .nav_bar nav a {
      float: left;
      margin-left: 14%;
      line-height: 60px;
      height: 60px; }
      .nav_bar nav a:first-child {
        margin-left: 0; }
  .nav_bar ul {
    position: static;
    bottom: 20px;
    width: auto;
    height: 60px;
    line-height: 60px;
    display: block;
    padding: 0;
    list-style: none;
    text-align: center;
    margin: 0;
    float: right; }
    .nav_bar ul li {
      float: right;
      height: 60px;
      line-height: 60px;
      margin: 0 10px; }
      .nav_bar ul li a {
        line-height: 60px;
        padding: 0; } }

@media (max-width: 767px) {
  .nav_bar nav a:hover:after {
    opacity: 0; }
  .nav_bar nav a:hover span {
    opacity: 1; }
  .nav_bar .logo {
    margin-left: 15px;
    margin-top: 5px; }
  .nav_bar nav {
    width: 51%;
    left: 41%; }
    .nav_bar nav a {
      margin-left: 11%; } }



